@import url(https://fonts.googleapis.com/css2?family=Arimo:ital,wght@0,400;0,700;1,400;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Header{position:fixed;top:0;left:0;right:0;display:flex;align-items:center;justify-content:space-between;padding:0 32px;height:80px;background:#fff;z-index:100;border-bottom:1px solid #111}.Header .logo{display:flex;align-items:center;cursor:pointer}.Header h1{font-size:1.8rem}.Header .App-logo{width:40px;height:40px;margin-right:.8rem;-webkit-animation:spin infinite 20s linear;animation:spin infinite 20s linear}.Header .cart-icon{cursor:pointer}@-webkit-keyframes spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@media(max-width: 768px){.Header{padding:0 16px;height:60px}}
.ShoppingContainer{display:flex;flex-direction:column;flex-wrap:wrap;margin-top:80px;padding:0;width:100%}.ShoppingList{list-style:none;display:flex;margin:0;padding:0;flex-wrap:wrap;width:100%;border-top:1px solid #111}@media(max-width: 768px){.ShoppingContainer{margin-top:60px}}
form{padding:1.6rem;display:flex;justify-content:center;width:100%;padding-left:.8rem;padding-right:.8em}form .radio{display:flex;padding-left:.8rem;padding-right:.8rem}form .radio label{display:flex;align-items:center;font-size:1.6rem;height:32px;font-weight:bold;padding-left:1.6rem;padding-right:1.6rem;background:#fff;color:#111;border-radius:1.6rem;cursor:pointer;align-self:flex-start;transition:ease-in .3s}form .radio label:hover{background:#f5f5f5}form .radio input{display:none}form .radio input:checked+label{background:#111;color:#fff}@media(max-width: 768px){form{overflow-x:scroll;justify-content:flex-start}form::-webkit-scrollbar{display:none}form .radio{white-space:nowrap}}
.ProductTile{position:relative;cursor:pointer;display:flex;flex-direction:column;width:33.3333333333%;padding:0;border:1px solid #111;border-left:none;border-top:none}.ProductTile:nth-of-type(3n){border-right:none}.ProductTile .image{object-fit:cover;object-position:center center;height:33vw}.ProductTile .InformationContainer{display:flex;flex-wrap:wrap;justify-content:space-between;align-items:center;margin-bottom:1.6rem;padding:.8rem 1rem;height:100%}.ProductTile .TextContainer{display:flex;flex-wrap:wrap;padding:.8rem 0 .8rem 1.6rem}.ProductTile h2{font-size:1.6rem;font-weight:normal;margin-right:1.6rem}.ProductTile h3{font-size:1.6rem;position:relative;margin-left:3.2rem;margin-right:.8rem}.ProductTile h3::before{content:"";position:absolute;background:#c8c8c8;margin-top:1.2rem;margin-left:-3.2rem;width:1.6rem;height:.1rem}.ProductTile .tag{position:absolute;display:flex;align-items:center;top:1.6rem;height:2.4rem;padding:0 .8rem;right:1.6rem;color:#111;background:#fff;border:1px solid #111;font-size:1.4rem;font-weight:bold;margin-bottom:.8rem;border-radius:1.2rem;display:flex;align-self:flex-start;justify-self:flex-end}.ProductTile .care{font-size:1.3rem}.ProductTile .Button{font-size:1.4rem;font-weight:bold;background:#fff;align-self:flex-end;margin:.4rem 0 .4rem auto;border:none;color:#111;padding-left:1.6rem;padding-right:1.6rem;height:32px;border-radius:20px;cursor:pointer;transition:ease-in .3s}.ProductTile .Button:hover{background:#111;color:#fff}@media(max-width: 768px){.ProductTile{width:50%}.ProductTile:nth-of-type(2n){border-right:none}.ProductTile:nth-of-type(3n){border-right:1px solid #111}.ProductTile .image{object-fit:cover;object-position:center center;height:60vw}.ProductTile .InformationContainer{flex-direction:column}.ProductTile .InformationContainer .TextContainer{display:flex;flex-direction:column;width:100%;padding:.8rem 0}.ProductTile h3{margin-left:0;margin-right:0}.ProductTile h3::before{display:none}.ProductTile .Button{margin-top:1.6rem;align-self:flex-end;border:1px solid #111}}
.Panel{display:flex;width:100%;flex-direction:column;align-items:flex-end;padding:0;color:#fff;border-bottom:1px solid #111;transition:ease .3s}.PanelContainer{display:flex;justify-content:space-between;width:100%;min-width:375px;height:100%;background-color:#fff;transition:width ease 300ms}.PanelContainer .image{width:60%;object-fit:cover;object-position:center center;height:calc(100vh - 80px)}.PanelContainer .InformationContainer{display:flex;flex-direction:column;align-items:flex-start;justify-content:center;padding:64px 96px;width:40%;color:#111;transition:ease 300ms}.PanelContainer .InformationContainer .TextContainer{width:100%}.PanelContainer .InformationContainer .TextContainer h1{font-size:4rem;margin-bottom:2.4rem}.PanelContainer .InformationContainer .TextContainer ul{list-style:none;margin-left:0;padding-left:0;font-size:1.6rem}.PanelContainer .InformationContainer .TextContainer ul li{margin-bottom:.8rem}.PanelContainer .InformationContainer .TextContainer h3{font-size:1.8rem;margin-bottom:8px}.PanelContainer .InformationContainer .TextContainer h4{font-size:1.6rem;margin-top:1.6rem;margin-bottom:3.2rem}.PanelContainer .InformationContainer .Button{font-size:1.4rem;font-weight:bold;background:#fff;border:1px solid #292929;color:#111;padding-left:2.4rem;padding-right:2.4rem;height:48px;border-radius:24px;cursor:pointer;transition:ease-in .3s}.PanelContainer .InformationContainer .Button:hover{background:#111;color:#fff}.cart-icon{position:relative}.cart-icon .cart-number{position:absolute;display:flex;justify-content:center;align-items:center;top:-4px;right:-4px;width:16px;height:16px;border-radius:8px;font-size:1rem;background:#0cc276;color:#fff}@media(max-width: 768px){.PanelContainer{flex-direction:column}.PanelContainer .image{width:100%;height:60vh}.PanelContainer .InformationContainer{width:100%;padding:3.2rem 1.6rem 6.4rem}.PanelContainer .InformationContainer .TextContainer h1{font-size:3.2rem}.PanelContainer .Button{width:100%;position:-webkit-sticky;position:sticky;bottom:1.6rem}}
.care{cursor:pointer;display:flex;align-items:center;font-size:1.6rem}.care p{font-size:1.6rem;margin-right:.8rem}
.mini-cart{position:fixed;top:0;bottom:0;z-index:101;color:#111;display:flex;flex-direction:column;justify-content:flex-end;width:100%;height:100vh;transition:ease-out 400ms}.mini-cart .mini-cart-container{position:absolute;display:flex;flex-direction:column;width:30%;height:100%;background:#fff;padding:2.4rem 3.2rem;transition:ease-out 400ms;overflow:auto}.mini-cart .mini-cart-container .title{display:flex;justify-content:space-between;width:100%;margin-bottom:3.2rem}.mini-cart .mini-cart-container h2{font-size:2rem;margin-right:16px}@media(max-width: 768px){.mini-cart .mini-cart-container{height:auto;width:100%;border-top:1px solid #111}}
.line-cart{display:flex;font-size:1.4rem;padding:1.6rem 0;border-bottom:1px solid #111}.line-cart:last-of-type{border-bottom:none}.line-cart-infos{display:flex;flex-direction:column;align-items:flex-start;flex:1 1;margin:1.6rem}h3{font-size:1.8rem;margin-bottom:.8rem}.mini-icon-container{width:2.4rem;height:2.4rem;display:flex;justify-content:center;align-items:center;cursor:pointer}.counter{display:flex;align-items:center;margin-bottom:16px;border:1px solid #111;height:3.2rem;border-radius:1.6rem;padding:0 .8rem}.counter p{font-size:1.6rem;font-weight:bold;margin:0 8px}.mini-image{object-fit:cover;object-position:center center;width:20%}.price{font-weight:bold}
.recomandation{position:fixed;z-index:101;bottom:16px;background:#0cc276;display:flex;padding:16px;max-width:343px;border:1px solid #111;overflow:hidden;transition:ease-in .3s}.recomandation ::before{content:"";width:4px;height:100%;background:#0cc276;position:absolute;top:0;left:0}.recomandation p{font-size:1.6rem;margin-right:16px}
.footer{display:flex;flex-direction:column;width:100%;justify-content:center;align-items:center;height:300px;background:#fff;border-top:1px solid #111;margin-top:-1px}.footer .footer-title{font-size:1.8rem;margin-bottom:3.2rem}.footer .form-container{min-width:343px}.footer .error{color:red;font-size:1.4rem;margin-left:2.4rem;margin-top:.8rem}.footer form{display:flex;justify-content:flex-start;align-items:center;font-size:1.6rem;background:#fff;color:#111;padding-left:.8rem;padding-right:.8rem;height:48px;border:1px solid #111;border-radius:24px}.footer input{display:flex;background:none;width:100%;padding-left:24px;padding-right:24px;font-size:1.6rem;border:none;outline-offset:none;outline-style:none}.footer button{border:none;font-size:1.4rem;font-weight:bold;background:#fff;color:#111;padding-left:1.6rem;padding-right:1.6rem;height:3.2rem;border-radius:20px;cursor:pointer;transition:ease-in .3s}.footer button:hover{background:#111;color:#fff}
.cart{position:fixed;top:0;bottom:0;width:100%;height:100vh;display:flex;flex-direction:column;align-items:flex-end;padding:0;color:#111;z-index:101;transition:background-color ease-out 300ms}.cart-container{position:absolute;display:flex;flex-direction:column;padding:2.4rem 3.2rem;width:30%;height:100%;color:#111;background-color:#fff;transition:ease-out 300ms}.cart-container h2{font-size:2rem}.cart-container ul{list-style:none;margin-left:0;padding-left:0;font-size:1.6rem}.cart-container ul li{margin-bottom:.8rem}.cart-container h3{font-size:1.6rem}.title{display:flex;justify-content:space-between;align-items:center;margin-bottom:3.2rem}.line-total{display:flex;justify-content:space-between;font-size:1.8rem;padding-top:1.6rem;border-top:1px solid #111}.line-total .price{font-weight:bold}@media(max-width: 768px){.cart{top:60px}.cart .cart-container{width:100%}}
*,::after,::before{box-sizing:border-box}#root{display:flex;flex-direction:column;min-height:100vh;height:100%}body,html{font-family:"Arimo","Helvetica Neue",Helvetica,Arial,sans-serif;font-size:10px;line-height:1.6;background-color:#fff;color:#111;-webkit-font-smoothing:antialiased}.App{display:flex;flex-direction:column;align-items:center;justify-content:flex-start;max-width:100%;min-height:100vh}.body{height:100%}.main{display:flex;flex:1 1;height:100%;flex-direction:column;justify-content:space-between}a,h1,h2,h3,h4,h5,h6,p{margin:0;line-height:150%}.icon{width:2.4rem;height:2.4rem;cursor:pointer}.icon .dark{color:#111}.mini-icon{width:16px;height:16px;cursor:pointer}.mini-icon .dark{color:#111}.cart-icon{position:relative}.cart-icon .cart-number{position:absolute;display:flex;justify-content:center;align-items:center;top:-4px;right:-4px;width:16px;height:16px;border-radius:8px;font-size:1rem;background:#0cc276;color:#fff}
