@import '../variables';

.Header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 32px;
    height: 80px;
    background: $light;
    z-index: 100;
    border-bottom: 1px solid $dark;

    .logo {
        display: flex;
        align-items: center;
        cursor: pointer;
    }

    h1 {
        font-size: 1.8rem;
    }

    .App-logo {
        width: 40px;
        height: 40px;
        margin-right: 0.8rem;
        animation: spin infinite 20s linear;
    }

    .cart-icon {
        cursor: pointer;
    }
}
@keyframes spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
@media (max-width: 768px) {
    .Header {
        padding: 0 16px;
        height: 60px;
    }

}
