@import '../variables';

.Panel {
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: flex-end;
    padding: 0;
    color: $light;
    border-bottom: 1px solid $dark;
    transition: ease 0.3s;
}

.PanelContainer {
    display: flex;
    justify-content: space-between;
    width: 100%;
    min-width: 375px;
    height: 100%;
    background-color: $light;
    transition: width ease 300ms;

    .image {
        width: 60%;
        object-fit: cover;
        object-position: center center;
        height: calc(100vh - 80px);
    }

    .InformationContainer {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        padding: 64px 96px;
        width: 40%;
        color: $dark;
        transition: ease 300ms;

        .TextContainer {
            width: 100%;
            
            h1 {
                font-size: 4rem;
                margin-bottom: 2.4rem;
            }
    
            ul {
                list-style: none;
                margin-left: 0;
                padding-left: 0;
                font-size: 1.6rem;
    
                li {
                    margin-bottom: 0.8rem;
                }
            }
    
            h3 {
                font-size: 1.8rem;
                margin-bottom: 8px;
            }

            h4 {
                font-size: 1.6rem;
                margin-top: 1.6rem;
                margin-bottom: 3.2rem;
            }
        }
        .Button {
            font-size: 1.4rem;
            font-weight: bold;
            background: $light;
            border: 1px solid #292929;
            color: $dark;
            padding-left: 2.4rem;
            padding-right: 2.4rem;
            height: 48px;
            border-radius: 24px;
            cursor: pointer;
            transition: ease-in 0.3s;
            &:hover {
                background: $dark;
                color: $light;
    
            }
    
        }
    }
}


.cart-icon {
    position: relative;

    .cart-number {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        top: -4px;
        right: -4px;
        width: 16px;
        height: 16px;
        border-radius: 8px;
        font-size: 1rem;
        background: #0CC276;
        color: $light;
    }
}

@media (max-width: 768px) {
    .PanelContainer {
        flex-direction: column;

        .image {
            width: 100%;
            height: 60vh;
        }

        .InformationContainer {
            width: 100%;
            padding: 3.2rem 1.6rem 6.4rem;

            .TextContainer {

                h1 {
                    font-size: 3.2rem;
                }
            }
        }
        .Button {
            width: 100%;
            position: sticky;
            bottom: 1.6rem;

        }
    }
}