@import '../variables';

.mini-cart {
    position: fixed;
    top: 0;
    bottom: 0;
    z-index: 101;
    color: $dark;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    width: 100%;
    height: 100vh;
    transition: ease-out 400ms;

    

    .mini-cart-container {
        position: absolute;
        display: flex;
        flex-direction: column;
        width: 30%;
        height: 100%;
        background: $light;
        padding: 2.4rem 3.2rem;
        transition: ease-out 400ms;
        overflow: auto;

        .title {
            display: flex;
            justify-content: space-between;
            width: 100%;
            margin-bottom: 3.2rem;
        }

        h2 {
            font-size: 2rem;
            margin-right: 16px;
        }
    }
}

@media (max-width: 768px) {
    .mini-cart {
    
        .mini-cart-container {
            height: auto;
            width: 100%;
            border-top: 1px solid $dark;
        }
    }
}




