@import '../variables';

.ShoppingContainer {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin-top: 80px;
    padding: 0;
    width: 100%;
}

.ShoppingList {
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
    flex-wrap: wrap;
    width: 100%;
    border-top: 1px solid $dark;
}
@media (max-width: 768px) {
    .ShoppingContainer {
        margin-top: 60px;
    }
}
