@import '../variables';

form {
    padding: 1.6rem;
    display: flex;
    justify-content: center;
    width: 100%;
    padding-left: 0.8rem;
    padding-right: 0.8em;

    .radio {
        display: flex;

        padding-left: 0.8rem;
        padding-right: 0.8rem;
        label {
            display: flex;
            align-items: center;
            font-size: 1.6rem;
            height: 32px;
            font-weight: bold;
            padding-left: 1.6rem;
            padding-right: 1.6rem;
            background: $light;
            color: $dark;
            border-radius: 1.6rem;
            cursor: pointer;
            align-self: flex-start;
            transition: ease-in 0.3s;
            &:hover {
                background: #F5F5F5;
            }
        }
        input {
            display: none;
        }
        input:checked + label {
            background: $dark;
            color: $light;
        }

    }
}

@media (max-width: 768px) {

        form {
            overflow-x: scroll;
            justify-content: flex-start;

            &::-webkit-scrollbar {
                display: none;
            }

            .radio {
                white-space: nowrap;

            }
        }
}
