@import '../variables';

.cart {
    position: fixed;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding: 0;
    color: $dark;
    z-index: 101;
    transition: background-color ease-out 300ms;
}

.cart-container {
    position: absolute;
    display: flex;
    flex-direction: column;
    padding: 2.4rem 3.2rem;
    width: 30%;
    height: 100%;
    color: $dark;
    background-color: $light;
    transition: ease-out 300ms;

    h2 {
        font-size: 2rem;
    }

    ul {
        list-style: none;
        margin-left: 0;
        padding-left: 0;
        font-size: 1.6rem;

        li {
            margin-bottom: 0.8rem;
        }
    }

    h3 {
        font-size: 1.6rem;
    }

}

.title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 3.2rem;
}

.line-total {
    display: flex;
    justify-content: space-between;
    font-size: 1.8rem;
    padding-top: 1.6rem;
    border-top: 1px solid $dark;

    .price {
        font-weight: bold;
    }
}
@media (max-width: 768px) {
    .cart {
        top: 60px;

        .cart-container {
            width: 100%;
        }
    }
}
