@import url( 'https://fonts.googleapis.com/css2?family=Arimo:ital,wght@0,400;0,700;1,400;1,700&display=swap');
@import 'variables';

*,
::after,
::before {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

#root {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    height: 100%;
}


body,
html {
    font-family: "Arimo","Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 10px;
    line-height: 1.6;
    background-color: $light;
    color: $dark;
    -webkit-font-smoothing: antialiased;
}

.App {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    max-width: 100%;
    min-height: 100vh;
}

.body {
    height: 100%;
}

.main {
    display: flex;
    flex: 1;
    height: 100%;
    flex-direction: column;
    justify-content: space-between;
}

a,
h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
    line-height: 150%;
}

.icon {
    width: 2.4rem;
    height: 2.4rem;
    cursor: pointer;

    .dark {
        color: $dark;
    }
}

.mini-icon {
    width: 16px;
    height: 16px;
    cursor: pointer;

    .dark {
        color: $dark;
    }
}

$dark: $dark;
$light: $light;

.cart-icon {
    position: relative;

    .cart-number {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        top: -4px;
        right: -4px;
        width: 16px;
        height: 16px;
        border-radius: 8px;
        font-size: 1rem;
        background: #0CC276;
        color: $light;
    }
}